.shop-header {
   border-bottom: 1px solid #e5e5e5;
   margin-bottom: 1rem;
   display: flex;
   justify-content: space-between;
}

.shop-header a {
   text-decoration: none;
}

.shop-header .logo {
   font-family: "Playfair Dispaly", Georgia, serif;
   font-size: 2.5rem;
   padding-left: 1rem;
}

.shop-header .shopping-cart {
   align-self: center;
   font-size: 1.3rem;
   padding-right: 1rem;
}

.shop-header .shopping-cart .cart-icon {
   font-size: 2.2rem;
   color: cadetblue;
   margin-right: 10px;
}
